@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Taviraj:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
  --font-family-default: 'Roboto', sans-serif;

  --clr-background-1: 0, 0%, 100%;
  --clr-background-2: 210, 22%, 89%;
  --clr-background-3: 210, 60%, 43%; /*  mid-blue */
  --clr-primary: 209, 100%, 25%; /* dark-blue */
  --clr-secondary: 206, 25%, 80%; /* light-blue */
  --clr-white: 0, 0%, 100%;
  --clr-gold: 37, 100%, 62%;
  --clr-burgundy: #861657;
  --clr-brick: #af253c;
  --clr-pink: #EF767A;


  --fs-100: .5rem;
  --fs-200: .875rem;
  --fs-300: 1rem;
  --fs-400: 1.125rem;
  --fs-500: 1.25rem;
  --fs-600: 1.5rem;
  --fs-700: 1.75rem;
  --fs-800: 2.5rem;
  --fs-900: 6rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  scroll-behavior: smooth;
}

body { 
  position: relative;
  overscroll-behavior: none;
  background-color: hsl(var(--clr-white));
  color: hsl(var(--clr-primary));
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  font-weight: 400;
}

section {
  position: relative;
  width: 100%;
  padding: 2rem 0;
}

.section-header {
  margin-bottom: 2rem;
  font-size: var(--fs-800);
  font-weight: 600;
  min-width: 100px;
}

.section-subhead {
  margin: 3rem 0 1rem;
  font-size: var(--fs-700);
  font-weight: 600;
  max-width: 700px;
}

/* 

.programs-section-background::after {
  content: "";
  width: 120%;
  height: calc(100% + 100px);
  position: absolute;
  top: 0;
  left: -5rem;
  background-image: linear-gradient(to right, #86165750, #86165750, #86165750);
  transform: skewY(5deg);
  border-radius: .5rem;
} */

.programs-section-background > * {
  position: relative;
  z-index: 2;
}

.back-btn {
  position: relative;
  display: flex;
  align-items: center;
  align-self: flex-start;
  position: fixed;
  top: 10.4rem;
  left: 2rem;
  cursor: pointer;
  z-index: 20;
  transition: all .2s ease-in-out;
}


.back-btn:hover {
  scale: 1.04;
}

.back-btn-icon {
  font-size: 1.7rem;
}

.single-page-layout {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  padding-top: 10rem;
}

.single-page-content {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 1400px;
}

.single-page-header {
  position: relative;
  align-self: flex-start;
  z-index: 1;
}

.unordered-list {
  list-style: disc;
  padding-left: 1rem;
}

.order-list {
  padding-left: 1.2rem;
  list-style: decimal;
}

.copy {
  line-height: 1.3;
  max-width: 900px;
  margin: 0 0 1rem 0 ;
}

.text-right {
  max-width: 100%;
  text-align: right;
}

.text-left {
  max-width: 100%;
  text-align: left;
}

.text-center {
  max-width: 100%;
  text-align: center;
}

.copy-bold {
  font-weight: 600;
}

.copy-thin {
  font-weight: 200;
}

.copy-bottom {
  margin-bottom: 7rem;
}

.block {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
  padding: 1.5rem;
  color: hsl(var(--clr-white));
  border-radius: .4rem;
  margin: 1rem 0 2rem;
  box-shadow: 6px 6px 10px hsl(var(--clr-primary), .4);
}

.btn-hover:hover {
  transform: scale(.98);
  box-shadow: inset 3px 3px 10px hsl(var(--clr-background-3), .4);
}

.underline {
  text-decoration: underline;
  text-underline-offset: 7px;
  text-decoration-thickness: 2px;
}

.link-blue-text {
  font-weight: 500;
  padding-bottom: 2px;
  color: hsl(var(--clr-primary));
  background-image: linear-gradient(hsl(var(--clr-primary)), hsl(var(--clr-primary)));
  background-position: 0 100%;
  background-size: 0% 1.5px;
  background-repeat: no-repeat;
  transition:
    background-size 0.2s,
    background-position 0s 0.2s;
  cursor: pointer;
}

.link-blue-text:hover {
  color: hsl(var(--clr-primary));
  background-position: 100% 100%;
  background-size: 100% 1.5px;
}

.link-white-text {
  padding-bottom: 2px;
  font-weight: 500;
  color: hsl(var(--clr-white));
  background-image: linear-gradient(hsl(var(--clr-white)), hsl(var(--clr-white)));
  background-position: 0 100%;
  background-size: 0% 1.25px;
  background-repeat: no-repeat;
  transition:
    background-size 0.2s,
    background-position 0s 0.2s;
  cursor: pointer;
}

.link-white-text:hover {
  color: hsl(var(--clr-white));
  background-position: 100% 100%;
  background-size: 100% 1.25px;
}

.link-red-text {
  padding-bottom: 2px;
  font-weight: 500;
  color: var(--clr-brick);
  background-image: linear-gradient(var(--clr-brick), var(--clr-brick));
  background-position: 0 100%;
  background-size: 0% 1.25px;
  background-repeat: no-repeat;
  transition:
    background-size 0.2s,
    background-position 0s 0.2s;
  cursor: pointer;
}

.link-red-text:hover {
  color: var(--clr-brick);
  background-position: 100% 100%;
  background-size: 100% 1.25px;
}

.navy-background {
  background-color: hsl(var(--clr-primary), .8);
}

.burgundy-background {
  background-color: #861657;
}

.burgundy-gradient {
  background-image:
    radial-gradient(
       circle closest-side,
      #d54499,
      #861657
    );
}

.burgundy-linear-gradient {
  background-image:
    linear-gradient(
      to bottom right,
      #d54499,
      #861657,
      #861657
    );
}

.brick-background {
  background-color: #af253c;
}

.brick-gradient {
  background-image:
    radial-gradient(
       circle closest-side,
      #e86b80,
      #af253c
    );
}

.brick-linear-gradient {
  background-image:
    linear-gradient(
      to bottom right,
      #e86b80,
      #af253c,
      #af253c
    );
}

.pink-background {
  background-color: var(--clr-pink);
}

.pink-gradient {
  background-image:
    radial-gradient(
       circle closest-side,
      #f7d0d2,
      var(--clr-pink)
    );
}

.pink-linear-gradient {
  background-image:
    linear-gradient(
      to bottom right,
      #f7d0d2,
      var(--clr-pink),
      var(--clr-pink)
    );
}

.lightblue-gradient {
  background-image:
  linear-gradient(
    to right,
  hsl(0, 0%, 98%),
  hsl(208, 27%, 90%),
  hsl(var(--clr-secondary))
);
}

.darkblue-linear-gradient {
  background-image:
  linear-gradient(
    to bottom right,
    hsl(var(--clr-background-2)),
    hsl(var(--clr-primary))
  );
}

.position-left {
  align-self: flex-start;
}

.position-right {
  align-self: flex-end;
}

.position-center {
  align-self: center;
}

.cta-btn {
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .5rem;
  padding: 1rem 2rem;
  font-weight: 500;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.program-btn {
  width: 250px;
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
  border-radius: .5rem;
  padding: 1rem 2rem;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 1.2;
  text-align: center;
  z-index: 10;
}

.contact-link {
  color: hsl(var(--clr-primary));
  text-decoration: underline;
  text-underline-offset: .2rem;
}

.contact-link:hover {
  color: #861657;
}

.master-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

/* INTRO SECTION */

.intro-section {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-image:
    linear-gradient(
      to right,
    hsl(0, 0%, 98%),
    hsl(208, 27%, 90%),
    hsl(var(--clr-secondary))
  );
}

.intro-img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.intro-header {
  position: absolute;
  top: 25%;
  left: 31%;
  color: hsl(var(--clr-primary));
  font-size: 7rem;
  font-weight: 600;
  text-shadow: 3px 3px 4px #676767;
}

.intro-copy-container {
  position: absolute;
  top: 65%;
  left: 60%;
  display: flex;
  align-items: center;
  width: 50%;
  text-shadow: 2px 2px 4px #ffffff;
}
/* 
.intro-line-graphic {
  background-color: hsl(var(--clr-primary));
  height: 2px;
  width: 30rem;
} */

.intro-copy {
  margin-left: 0rem;
  font-size: 1.2rem;
  line-height: 1.2;
  max-width: 24rem;
}

.intro-cta {
  position: absolute;
  left: 3rem;
  bottom: 40px;
  background-color: hsl(var(--clr-white));
  color:hsl(var(--clr-primary));
  box-shadow: -2px 2px 5px hsl(var(--clr-white), .1);
  font-size: 1.3rem;
  font-weight: 400;
  z-index: 2;
  animation: all .1s ease-in-out;
}

.intro-cta:hover {
  background-color: hsl(var(--clr-primary));
  color:hsl(var(--clr-white));
}

/* QUOTE BANNER */

.quote-banner {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 250px;
  padding: 1.5rem 0rem;
  font-size: 1.7rem;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  line-height: 1.3;
  color: hsl(var(--clr-white));
  
}

.quote-background {
  max-width: 1000px;
}

.quote-banner span {
  padding-right: 0rem;
  color: hsl(var(--clr-primary));
  font-family: 'Taviraj', serif;
  font-size: 4.2rem;
  font-weight: 600;
  text-decoration:underline;
  text-underline-offset: 6px;
  text-decoration-thickness: 1.5px;
  line-height: 1;
}
