/* APPROACH SECTION */

.approach-section {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 7rem 0 6rem;
}

.approach-header {
  color: hsl(var(--clr-primary))
}

.approach-content {
  width: calc(100% - 12rem);
  height: 100%;
}

.approach-graphic {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}

.approach-team {
  position: relative;
  margin: 3rem 1rem;
}

.approach-background {
  display: flex;
  justify-content: center;
  max-width: 380px;
  align-items: center;
  height: 250px;
  color: hsl(var(--clr-white));
  box-shadow: 2px 2px 15px #5a5858b5;
}

.approach-highlight {
  padding: 1rem 5rem;
  margin: 2rem 0 8rem;
  max-width: 1100px;
  font-size: 1.4rem;
  font-weight: 500;
  text-align: center;
  border-top: 2px solid hsl(var(--clr-primary));
  border-bottom: 2px solid hsl(var(--clr-primary));
}

.approach-content-bottom {
  padding: 4rem 10rem;
  width: 100%
}

.approach-content-container {
  display: grid;
  gap: 1.5rem;
  grid-template-columns: repeat(10 auto);
  grid-template-rows: repeat(6 auto);
}

.approach-copy.last {
  margin-bottom: 2rem;
}

.approach-copy-bottom {
  margin-bottom: 0;
}

.approach-img-container-main {
  position: relative;
  grid-column: 1 / 8;
  grid-row: 1 / 7;
  max-width: 100%;
  min-width: 100%;
  min-height: 100%;
  max-height: 100%;
}

.approach-copy-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-column: 8 / 11;
  grid-row: 1 / 4;
  padding: 1.5rem;
  background-color: hsl(var(--clr-white), .9);
  color: var(--clr-pink);
  font-size: 1.4rem;
  font-weight: 300;
  border-radius: .2rem;
}

.approach-mom-img {
  max-width: 100%;
  min-width: 100%;
  min-height: 100%;
  max-height: 100%;
  grid-column: 8 / 11;
  grid-row: 4 / 7;
  border-radius: .2rem;
  object-fit: cover;
}

.approach-family-img {
  object-fit: cover;
  max-width: 100%;
  min-width: 100%;
  min-height: 100%;
  max-height: 100%;
  border-radius: .2rem;
  margin-bottom: 0;
  display: block;
}

.approach-copy-quote {
  position: absolute;
  bottom: 120px;
  left: 0;
  padding: 3.5rem 3rem 2rem;
  background-color: hsl(var(--clr-white), .9);
  color: var(--clr-pink);
  font-size: 1.4rem;
  font-weight: 300;
  line-height: 1.2;
}

.approach-quote-left {
  position: absolute;
  top: 1rem;
  left: 1rem;
  font-size: 2.2rem;
  margin-right: .4rem;
}

.approach-quote-right {
  position: absolute;
  font-size: 2.2rem;
  bottom: 2rem;
  right: 2rem;
}

.team-img {
  height: 300px;
  z-index: 10;
}

.team-copy {
  max-width: 330px;
  font-size: 1.7rem;
  padding: 1rem;
  font-weight: 300;
  z-index: 9;
}

/* PROGRAMS HOME SECTION */

.what-we-do-section {
  padding-top: 0;
  padding-bottom: 0;
}

.programs-section-background {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 2rem 0 3rem;
}

.intro-content-container {
  position: relative;
  padding-top: 10rem;
  z-index: 1;
}

#social-workers,
#mentor-parents,
#corridor {
  position: absolute;
  top: -10rem;
}

.programs-header {
  max-width: 700px;
}

.prevention-header {
  max-width: 700px;
  padding: 2rem;
  line-height: 1.1;
  border-radius: .2rem;
}

.prevention-services-content {
  display: flex;
  width: 100%;
}

.prevention-services-content > div {
  padding: 1.5rem;
  margin-bottom: 2rem;
  background-image: 
  linear-gradient(
  to bottom right,
  hsl(var(--clr-white), .6),
  hsl(var(--clr-white), .7),
  hsl(var(--clr-white), .9),
  hsl(var(--clr-white), 1)
  );
  color: var(--clr-brick);
  border-radius: .2rem;
}

.prevention-services-content > * {
  line-height: 1.3;
}

.prevention-services-content > div:first-of-type {
  margin-right: 2rem;
  min-width: 315px;
  background-image: 
  linear-gradient(
    to bottom right,
    hsl(var(--clr-white), .5),
    hsl(var(--clr-white), .7),
    hsl(var(--clr-white), .9),
    hsl(var(--clr-white), 1)
  );
}

.prevention-services-content-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  background-color: #af253cbb;
  color: hsl(var(--clr-white));
  font-weight: 500;
  border-radius: .2rem;
  padding: 1.5rem;
  background-image: 
  linear-gradient(
    to bottom right,
    hsl(var(--clr-white), .6),
    hsl(var(--clr-white), .7),
    hsl(var(--clr-white), .9),
    hsl(var(--clr-white), 1)
  );
  color: var(--clr-brick);
  border-radius: .2rem;
}

.prevention-copy {
  max-width: 700px;
  line-height: 1.3;
  margin-bottom: 1rem;
}

.prevention-span {
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.corridor-icon {
  margin-right: .5rem;
}

.prevention-link .link-red-text {
  font-weight: 300;
  margin-bottom: .4rem;
}
 
.prevention.section-subhead {
  margin: -2rem 0 3rem;
}

.prevention-subhead {
  font-size: 1.4rem;
  font-weight: 500;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-thickness: .7px;
}

.prevention-services-subhead {
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 500;
  text-decoration: underline;
  text-align: center;
  text-underline-offset: 3px;
}

.mentor-section {
  position: relative;
  color: hsl(var(--clr-white));
  z-index: 1;
}

.programs-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 4rem;
  margin-bottom: 6rem;
  background-color: hsl(var(--clr-gold), .3);
}

.programs-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1400px;
  margin: 3rem 2rem;
}

.program-content-container {
  align-self: center;
  position: relative;
  max-width: 1000px;
  width: 100%;
}

.program-content-container .copy { 
  max-width: 1000px;
}

.program-container {
  display: flex;
  margin-bottom: 2rem;
}

.programs-img {
  width: 350px;
  height: 300px;
  object-fit: cover;
  margin-bottom: .5rem;
  border-radius: .2rem;
}

.img-float-left {
  float: left;
  margin-right: 2rem;
  margin-bottom: 1rem;
}

.img-float-right {
  float: right;
  margin-left: 2rem;
  margin-bottom: 1rem;
}

.programs.section-subhead {
    margin: 3rem 0 1rem;
    padding-bottom: .2rem;
    font-size: 2rem;
    font-weight: 500;
    max-width: 100%;
    text-underline-offset: 5px;
    border-bottom: 1.5px solid hsl(var(--clr-primary));
}

.programs.section-subhead-white {
  margin: 3rem 0 1rem;
  padding-bottom: .2rem;
  font-size: 2rem;
  font-weight: 500;
  max-width: 100%;
  text-underline-offset: 5px;
  border-bottom: 1.5px solid hsl(var(--clr-white));
}

.subhead-container {
  display: flex;
  width: 100%;
}

.programs-subhead {
  max-width: 700px;
  padding: 2rem;
  margin-top: 2rem;
  font-weight: 400;
  color: hsl(var(--clr-white));
  line-height: 1.1;
  border-radius: .2rem;
  text-align: center;
  font-size: var(--fs-700);
}

.programs-copy {  
  max-width: 35rem;
  transition: all .3s ease-in-out;
  background-color: hsl(var(--clr-white), .7);
}

.programs-copy.copy {
  margin-bottom: 0;
}

.social-worker-section {
  position: relative;
  padding: 2rem 0 6rem;
  color: hsl(var(--clr-white));
  z-index: 1;
}

.corridor-section .programs.section-subhead,
.social-worker-section .programs.section-subhead, 
.mentor-section .programs.section-subhead{
  border-bottom: 1.5px solid hsl(var(--clr-white));
}

.first-call-section {
  width: 100%;
}

.first-call-hours {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 700px;
  padding: 2rem;
  margin: 2rem auto;
  background-image: 
  linear-gradient(
  to bottom right,
  hsl(var(--clr-white), .3),
