/* CONTACT SECTION */
.contact-section {
  position: relative;
  padding: 6rem 10rem;
  background-image: 
    linear-gradient(
    to bottom right,
    hsl(210, 25%, 94%),
    hsl(var(--clr-background-2)),
    hsl(var(--clr-background-2))
  );
  color: hsl(var(--clr-primary));
}

.contact-content {
  position: relative;
  display: flex;
  justify-content: space-around;
  border-radius: .3rem;
  background-color: hsl(var(--clr-secondary));
}

.contact-content-left,
.contact-content-right {
  width: 45%;
  padding: 3rem 2rem;
}

.contact-map-img {
  width: 100%;
  margin: 1.5rem 0 2rem;
  border-radius: .7rem;
}

.contact-copy {
  margin-bottom: 1rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: .4rem;
  margin-top: 2rem;
  font-weight: 400;
}

.contact-form input,
.contact-form textarea {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: hsl(var(--clr-primary));
}

.contact-form textarea {
  padding: 1rem;
}

.select-label {
  display: flex;
  flex-direction: column;
}

.contact-form select {
  font-family: var(--font-family-default);
  font-size: 1.1rem;
  color: hsl(var(--clr-primary));
  margin-top: 1rem;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
  text-indent: 1px;
  text-overflow: '';
}

.contact-form select::placeholder,
.contact-form input::placeholder,
.contact-form textarea::placeholder {
  font-family: var(--font-family-default);
  font-size: var(--fs-400);
  line-height: 1.2;
  color: hsl(var(--clr-primary));
  font-weight: 400;
}

.contact-form input:focus, 
.contact-form select:focus, 
.contact-form textarea:focus {
  outline: none;
} 

.contact-form input, 
.contact-form select {
  min-width: 100%;
  background-color: hsl(var(--clr-white));
  padding: .7rem 1rem;
  margin-bottom: 1.5rem;
  border: none;
  border-radius: .3rem;
}

.contact-form input.input-error,
.contact-form select.input-error {
  border: 1.5px solid #e10000;
  border-radius: .5rem;
}

.contact-form textarea {
  min-width: 100%;
  max-width: 100%;
  height: 227px;
  padding-top: .5rem;
  margin: 0 0 2.5rem;
  background-color: hsl(var(--clr-white));
  border: none;
  border-radius: .4rem;
  resize: none;
}

.contact-form textarea.input-error {
  border: 1.5px solid #e10000;
}

.contact-btn {
  align-self: flex-start;
  background-color: hsl(var(--clr-primary));
  color: hsl(var(--clr-white));
  border: none;
  border-radius: .7rem;
  padding: 1rem 2rem;
  font-size: 1.1rem;
  cursor: pointer;
  transition: all .25s ease-in-out;
}

.contact-btn:hover {
  align-self: flex-start;
  background-color: hsl(var(--clr-white));
  color: hsl(var(--clr-primary));
}


select.contactGroupField {
  border: 2px solid hsl(var(--clr-primary));
  animation: border-pulsate 2.5s infinite;
}

@keyframes border-pulsate {
  0%   { border-color:hsl(var(--clr-primary)); }
  50%  { border-color:hsl(var(--clr-secondary)) }
  100% { border-color:sl(var(--clr-primary)); }
}

.success-popup {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 40rem;
  padding: 2rem;
  background-color: hsl(var(--clr-accent), .4);
  border-radius: .4rem;
  color:hsl(var(--clr-white));
  text-align: center;
  opacity: 0;
  top: -200px;
  transform: translate(-50%, -50%) scale(0.5);
  transition: opacity 300ms ease-in-out,
              top 500ms ease-in-out,
              transform 500ms ease-in-out;
}

.checkmark-background {
  position: relative;
  width: 3rem;
  height: 3rem;
  background-color: #fff;
  border-radius: 50%;
}

.success-checkmark {
  position: absolute;
  top: 0;
  left: 0;
  font-size: var(--fs-900);
  color: hsl(var(--clr-accent));
}

.success-message {
  color: hsl(var(--clr-accent));
}

.success-thanks {
  margin-bottom: .8rem;
}

.success-btn {
  margin-top: 3.5rem;
}

