  position: absolute;
  bottom: 17.5rem;
  width: 230px;
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
}

.mission-graphic-container {
  display: flex;
  justify-content: space-between;
  margin: 5rem 0;
  width: 100%;
  max-width: 1000px;
}

.mission-step:nth-child(2) {
  margin: 0 1rem;
}

.mission-step {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 310px;
  min-height: 180px;
  margin: 0rem;
  padding: 2rem;
  color: hsl(var(--clr-white));
  border: 2px solid hsl(var(--clr-primary));
  border-radius: .2rem;
  box-shadow: 10px 10px 10px hsl(var(--clr-primary), .2);
}

.mission-header {
  position: absolute;
  top: -2.5rem;
  left: 1rem;
  text-transform: uppercase;
  font-size: 4.3rem;
  font-weight: 500;
  display: inline-block;
  transform: scale(1, 1.3);
  -webkit-transform: scale(1, 1.3);
  -moz-transform: scale(1, 1.3);
  -ms-transform: scale(1, 1.3);
  -o-transform: scale(1, 1.3);
}

.mission-step:nth-child(1) .mission-header {
  text-shadow: 2px 2px 4px hsla(325, 72%, 31%, 0.701);
}

.mission-step:nth-child(2) .mission-header {
  text-shadow: 2px 2px 4px hsla(358, 79%, 70%, 0.701);
}

.mission-step:nth-child(3) .mission-header {
  text-shadow: 2px 2px 4px hsl(var(--clr-primary), .7);
}

.mission-step-graphic {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: .5rem;
}

.mission-step-line {
  width: 95%;
  margin: .7rem 0;
  border: 1px solid hsl(var(--clr-primary));
  box-shadow: 1px 1px 3px hsl(var(--clr-white), .4);
}

.mission-step-icon {
  position: absolute;
  right: 0;
  color: hsl(var(--clr-primary));
}

.mission-step-icon.dot {
  font-size: .9rem;
}

.mission-step-copy {
  font-size: 1.3rem;
  font-weight: 300;
  line-height: 1.1;
}


/* Family Defense */

