}

/* FINANCIALS PAGE */

.financials-section {
  padding-bottom: 0;
}

.financials-content {
  width: 100%;
  padding: 0 10rem;
  font-size: 1rem;
}

.financials-graphic {
  margin-right: 7rem;
}

.financials-graphic-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 1rem;
  align-items: center;
  width: 100%;
}

.financials-content-bottom {
  position: relative;
  width: 100%;
  height: 100%;
  color: hsl(var(--clr-white));
  padding: 2rem 14rem 0;
  margin-top: 5rem;
  font-size: 1rem;
  font-weight: 300;
  z-index: 1;
}

.financials-container {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-height: 400px;
  margin: 0 auto 1rem;
  padding: 1rem 0;
  z-index: 1;
}

.two-column {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 235px));
  grid-auto-rows: 2rem;
  gap: .6rem 1rem;
}

.two-column > * {
  margin: auto 0;
}

.three-column {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  gap: .2rem 1.5rem;
  padding: 1rem 0;
}

.three-column > div {
  margin-bottom: 2rem;
  align-self: center;
}

.financials-table-header {
  grid-column: 1 / span 2;
}

.financials-table-footer {
  text-transform: uppercase;
}

.donut-graph {
  width: 100%;
  height: 100%;
  max-width: 500px;
  max-height: 500px;
}

.financials-subhead {
  font-size: 1.1rem;
  font-weight: 500;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-thickness: .7px;
}

.financials-copy-bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.main-table-footer {
  align-self: flex-end;
  text-align: right;
  font-size: 1.4rem;
  border: .7px solid hsl(var(--clr-white));
  padding: 1rem;
}

.financials-table-footer {
  font-weight: 500;
  text-decoration: overline;
  text-decoration-thickness: .7px;
}

.financials-downloads {
  margin-bottom: 1rem;
  font-weight: 200;
}

.financials-downloads a {
  font-weight: 400;
}

/* DONATE PAGE */

.donate-section {
  padding-bottom: 10rem;
}

.donate-content {
  display: flex;
  max-width: 1000px;
}

.donate-content > * {
  width: 50%;
}

.donate-copy-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem;
}

.donate-copy-container > * {
  margin-bottom: 1.5rem;
}

.donate-header {
