  padding: 3.5rem 2rem;
  background-color: hsl(var(--clr-primary), .7);
  color: hsl(var(--clr-white));
  font-style: italic;
  font-size: 1.4rem;
  font-weight: 300;
  text-align: center;
  line-height: 1.2;
  border-bottom-left-radius: .2rem;
