}

.stat-2 .line-3 {
  font-size: 2.3rem;
}

.stat-3 .line-1{
  font-size: 2.3rem;
}

.stat-3 .line-2 {
  font-size: 2.3rem;
  font-weight: 600;
}

.stat-3 .line-3 {
  font-size: 1.85rem;
}



.impact-stats-container li:nth-child(2) {
