.mailing-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  color: hsl(var(--clr-white));
  border-bottom-left-radius: .3rem;
  border-top-left-radius: .3rem;
  margin: 4rem 0 5rem;
}

.mailing-list-copy {
