  }

  .intro-copy-container {
    color: hsl(var(--clr-white));
    text-shadow: 4px 4px 9px #5a5858;
    top: 50%;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
  }

  .intro-line-graphic {
    display: none;
  }

  .intro-copy {
    margin-left: 2rem;
    max-width: 24rem;
  }

  .quote-banner {
    margin-top: 3rem;
  }

  .programs-section {
    padding: 2rem;
  }

  .program-container {
    flex-direction: column;
  }

  .program-container:nth-child(odd) {
    flex-direction: column;
  }

  .programs-img-1,
  .programs-img-2,
  .programs-img-3,
  .programs-img-4,
  .programs-img-5 {
    width: 100%;
    border-radius: 0;
  }

  .program-1 .programs-copy,
  .program-2 .programs-copy,
  .program-3 .programs-copy,
  .program-4 .programs-copy
  .program-5 .programs-copy {
    padding: 1rem;
  }

  .rights-banner {
    flex-direction: column;
    font-size: 1.3rem;
    white-space:nowrap;
    margin: 1.5rem 0;
  }

  .rights-banner > * {
    margin: .3rem 0;
  }

  .rights-cta {
    padding: 1rem;
    font-size: 1.2rem;
    font-weight: 300;
    cursor: pointer;
  }

  .news-carousel {
    margin: 2rem 0;
  }

  .carousel-item {
    padding: 1rem;
    justify-content: center;
    min-width: 100%;
  }

  .carousel-item-img {
    align-self: center;
    min-height: 50vw;
  }

  .news-footer {
    padding: 0rem;
  }

  .news-social-links {
    justify-content: flex-start;
    width: 100%;
  }

  .news-social-copy {
    max-width: 50%;
  }

  .contact-content {
    flex-direction: column;
  }

  .contact-content-left,
  .contact-content-right {
    width: 100%;
    padding: 1rem 2rem;
  }

  .footer-content {
    flex-direction: column;
  }

  .footer-content > * {
    align-self: center;
  }

  .footer-content .footer-right a {
    margin-left: 0;
  }

  .footer-right {
    text-align: left;
  }

  .footer-right > * {
    margin: 0;
  }

  .footer-right-bottom {
    justify-content: center;
  }

  .staff-page-layout {
    margin-top: 6rem;
  }

  .staff-copy {
    text-align: left;
  }

  .board-profile {
    flex-direction: column;
  }

  .board-info-container {
    padding: 0;
    margin-bottom: 2rem;
  }

  .board-profile:nth-of-type(even) {
    flex-direction: column;
  }

  .board-profile:nth-of-type(even) .board-info-container {
    padding: 0;
  }

  .board-img {
    max-height: initial;
    width: 100%;
    margin-bottom: 2rem;
  }


  /* ABOUT, MISSION, VISION PAGE */

  .mission-graphic-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;   
  }

  .mission-step {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    min-height: 220px;

  }

  .mission-step:nth-child(2) {
    margin: 2rem 0;
  }

  .mission-step-line {
    width: 98%;
  }

}



